"use client"

import { useState, useEffect } from "react"
import { CurtainSidebar } from "@/components/CurtainSidebar"
import { GlassHeader } from "@/components/GlassHeader"
import { FloatingParticles } from "@/components/FloatingParticles"
import { MagneticMetricCard } from "@/components/MagneticMetricCard"
import { GeoDataPulse } from "@/components/GeoDataPulse"
import { MassiveNeuralCore } from "@/components/MassiveNeuralCore"
import { LiveTerminal } from "@/components/LiveTerminal"
import { LiquidLightPipeline } from "@/components/LiquidLightPipeline"
import { ScannerJobsTable } from "@/components/ScannerJobsTable"
import { Footer } from "@/components/Footer"
import { MetricCardSkeleton, JobsTableSkeleton } from "@/components/skeleton-loaders"
import { Card } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { dashboardMetrics, mockJobs, metricTooltips } from "@/lib/mock-data"
import { Users, Target, Briefcase, AlertTriangle, Menu, Activity } from "lucide-react"

export default function DashboardPage() {
  const [isLoading, setIsLoading] = useState(true)
  const [sidebarOpen, setSidebarOpen] = useState(false)

  useEffect(() => {
    const timer = setTimeout(() => setIsLoading(false), 1200)
    return () => clearTimeout(timer)
  }, [])

  const recentJobs = mockJobs.slice(0, 4)

  return (
    <div className="w-full min-h-screen overflow-x-hidden bg-background">
      {/* Floating particles */}
      <FloatingParticles />

      {/* Curtain Sidebar */}
      <CurtainSidebar isOpen={sidebarOpen} onClose={() => setSidebarOpen(false)} />

      {/* Header with hamburger */}
      <header className="sticky top-0 z-40 w-full border-b border-border/50 bg-background/80 backdrop-blur-lg">
        <div className="container mx-auto px-4 py-4 flex items-center justify-between">
          <div className="flex items-center gap-4">
            <button
              onClick={() => setSidebarOpen(true)}
              className="p-2 rounded-lg hover:bg-muted transition-colors"
              aria-label="Open menu"
            >
              <Menu className="w-6 h-6" />
            </button>
            <div>
              <h1 className="text-xl font-bold">
                DataCure <span className="text-emerald-500">Command Center</span>
              </h1>
              <p className="text-xs text-muted-foreground">AI-Powered Provider Validation</p>
            </div>
          </div>

          <div className="flex items-center gap-4">
            <Badge variant="outline" className="bg-emerald-500/10 text-emerald-500 border-emerald-500/30">
              <Activity className="w-3 h-3 mr-1 animate-pulse" />
              System Active
            </Badge>
          </div>
        </div>
      </header>

      {/* Main content - FULL WIDTH */}
      <main className="relative z-10">
        {/* Hero KPIs */}
        <section className="container mx-auto px-4 py-8">
          <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-4">
            {isLoading ? (
              <>
                <MetricCardSkeleton />
                <MetricCardSkeleton />
                <MetricCardSkeleton />
                <MetricCardSkeleton />
              </>
            ) : (
              <>
                <MagneticMetricCard
                  title="Total Providers Validated"
                  value={dashboardMetrics.totalProvidersValidated.toLocaleString()}
                  change={dashboardMetrics.totalProvidersChange}
                  changeLabel="vs last month"
                  icon={<Users className="h-5 w-5 text-muted-foreground" />}
                  tooltip={metricTooltips.totalProviders}
                />
                <MagneticMetricCard
                  title="Average Confidence Score"
                  value={`${dashboardMetrics.averageConfidenceScore}%`}
                  change={dashboardMetrics.confidenceChange}
                  changeLabel="vs last month"
                  icon={<Target className="h-5 w-5 text-muted-foreground" />}
                  tooltip={metricTooltips.averageConfidence}
                />
                <MagneticMetricCard
                  title="Active Jobs"
                  value={dashboardMetrics.activeJobs}
                  change={dashboardMetrics.activeJobsChange}
                  changeLabel="vs yesterday"
                  icon={<Briefcase className="h-5 w-5 text-muted-foreground" />}
                  tooltip={metricTooltips.activeJobs}
                />
                <MagneticMetricCard
                  title="Pending Manual Review"
                  value={dashboardMetrics.recordsRequiringReview}
                  change={dashboardMetrics.reviewChange}
                  changeLabel="vs last week"
                  icon={<AlertTriangle className="h-5 w-5 text-muted-foreground" />}
                  tooltip={metricTooltips.recordsReview}
                />
              </>
            )}
          </div>
        </section>

        {/* Geo Data Pulse */}
        <GeoDataPulse />

        {/* MASSIVE Neural Core */}
        <MassiveNeuralCore />

        {/* Live Terminal */}
        <LiveTerminal />

        {/* Liquid Light Pipeline */}
        <LiquidLightPipeline />

        {/* Jobs Table */}
        <section className="container mx-auto px-4 py-12">
          {isLoading ? (
            <Card className="bg-card border-border p-6">
              <JobsTableSkeleton rows={4} compact />
            </Card>
          ) : (
            <ScannerJobsTable jobs={recentJobs} />
          )}
        </section>
      </main>

      {/* Footer */}
      <Footer />
    </div>
  )
}
